queue-list {
  display: block; }
  queue-list ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    queue-list ul li {
      padding: 15px;
      border-bottom: 1px solid #6988c7;
      font-weight: 600;
      font-size: 80%;
      display: flex;
      align-items: center; }
      queue-list ul li:hover {
        cursor: pointer;
        background: #d5e6e9; }
      queue-list ul li img {
        margin-right: 15px;
        width: 120px; }
        .popup queue-list ul li img {
          width: 80px; }
      queue-list ul li p {
        font-size: 140%;
        margin: 0; }
